<template>
    <div class="setting-date">
        <div class="setting-row">
            <h3>Exam {{ index }}</h3>
            <button @click="remove">-</button>
        </div>
        <div class="setting-row">
            <label>Title</label>
            <input type="text" v-model="title" @change="update">
        </div>
        <div class="setting-row">
            <label>Date</label>
            <input type="date" v-model="date" @change="update">
        </div>
    </div>
</template>

<script>
export default {
    name: 'SubSettingExam',
    props: {
        index: Number,
        exam: {
            title: String,
            date: String,
        },
    },
    data() {
        return {
            title: this.exam.title,
            date: this.exam.date,
        };
    },
    methods: {
        update() {
            var newExam = {
                title: this.title,
                date: this.date,
            };
            this.$emit('update', this.index, newExam);
        },
        remove() {
            this.$emit('remove', this.index);
        }
    },
}
</script>