<template>
    <div id="setting-exams">
        <div class="setting-toggle">
            <h2 @click="isOpen = !isOpen">Exam Dates</h2>
        </div>
        <div class="setting-list">
            <SubSettingExam v-for="(exam, i) in exams"
                            :exam="exam"
                            :index="i"
                            :key="i"
                            @update="update"
                            @remove="remove"></SubSettingExam>
        </div>
        <button @click="add">+</button>
    </div>
</template>

<script>
import SubSettingExam from './sub/SubSettingExam.vue'

export default {
    name: 'SettingExam',
    props: {
        parentExams: Array,
    },
    data() {
        return {
            exams: this.parentExams,
        };
    },
    components: {
        SubSettingExam,
    },
    methods: {
        add() {
            this.$emit('add');
        },
        update(index, newExam) {
            this.$emit('update', index, newExam);
        },
        remove(index) {
            this.$emit('remove', index);
        }
    }
}
</script>