<template>
    <div class="setting-sub">
        <h3>{{ name }}</h3>
        <div class="setting-row">
            <label>Font Type</label>
            <select v-model="fontFamily" @change="update">
                <option v-for="(font, i) in fontFamilies" :value="font" :key="i">
                    {{ font }}
                </option>
            </select>
        </div>
        <div class="setting-row">
            <label>Font Size</label>
            <input type="number" min="1" v-model="fontSize" @change="update">px
        </div>
        <div class="setting-row">
            <label>Font Color</label>
            <input type="color" value="#ff0000" v-model="color" @change="update">
        </div>
        <div class="setting-row">
            <label>Background Color</label>
            <input type="color" value="#ff0000" v-model="backgroundColor" @change="update">
        </div>
    </div>
</template>

<script>
export default {
    name: 'SubSettingStyle',
    props: {
        name: String,
        userStyle: {
            'font-family': String,
            'font-size': Number,
            'color': String,
            'background-color': String, 
        },
    },
    data() {
        return {
            fontFamilies: [
                "arial",
                "verdana",
                "georgia",
            ],
            fontFamily: this.userStyle['font-family'],
            fontSize: this.userStyle['font-size'],
            color: this.userStyle['color'],
            backgroundColor: this.userStyle['background-color'],
        };
    },
    methods: {
        update() {
            var newStyle = {
                'font-family': this.fontFamily,
                'font-size': this.fontSize,
                'color': this.color,
                'background-color': this.backgroundColor,
            };
            this.$emit('update', newStyle);
        },
    },
}
</script>