<template>
    <div id="setting-animation">
        <div class="setting-toggle">
            <h2 @click="isOpen = !isOpen">Animation</h2>
        </div>
        <div class="setting-list" v-show="isOpen">
            <div class="setting-row">
                <label>Animation Type</label>
                <select v-model="animationType" @change="update">
                    <option v-for="(types, i) in animationTypes" :value="types" :key="i">
                        {{ types }}
                    </option>
                </select>
            </div>
            <div class="setting-row">
                <label>Animation Speed</label>
                <input type="number" min="0.1" step="0.1" v-model="animationSpeed" @change="update">sec
            </div>
            <div class="setting-row">
                <label>Animation Interval</label>
                <input type="number" min="1" v-model="animationInterval" @change="update">sec
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: 'SettingAnimation',
    props: {
        animation: Object,
    },
    data() {
        return {
            isOpen: true,
            animationTypes: [
                "slide",
            ],
            animationType: this.animation.type,
            animationSpeed: this.animation.speed,
            animationInterval: this.animation.interval,
        };
    },
    methods: {
        update() {
            var newAnimation = {
                type: this.animationType,
                speed: this.animationSpeed,
                interval: this.animationInterval,
            };
            this.$emit('update', newAnimation);
        }
    },
}
</script>