<template>
  <div id="app">
    <UniqueId></UniqueId>
    <SettingExam
      :parentExams="widgetData.exams"
      @add="addExam"
      @update="updateExam"
      @remove="removeExam"
    ></SettingExam>
    <SettingFormat :format="widgetData.format" @update="updateFormat"></SettingFormat>
    <SettingStyle :userStyle="widgetData.style" @update="updateStyle"></SettingStyle>
    <SettingAnimation :animation="widgetData.animation" @update="updateAnimation"></SettingAnimation>
  </div>
</template>

<script>
import UniqueId from "./components/UniqueId.vue";
import SettingExam from "./components/SettingExam.vue";
import SettingFormat from "./components/SettingFormat.vue";
import SettingAnimation from "./components/SettingAnimation.vue";
import SettingStyle from "./components/SettingStyle.vue";

export default {
  name: "app",
  data() {
    return {
      widgetData: {
        exams: [],
        format: {
          dday: "D-28 (D-%d)",
          date: "2019.02.19 (YYYY.MM.DD)"
        },
        animation: {
          type: "slide",
          speed: 0.1,
          interval: 4
        },
        style: {
          title: {
            "font-family": "arial",
            "font-size": 5,
            color: "#ff0000",
            "background-color": "#ff0000"
          },
          dday: {
            "font-family": "arial",
            "font-size": 5,
            color: "#ff0000",
            "background-color": "#ff0000"
          },
          date: {
            "font-family": "arial",
            "font-size": 5,
            color: "#ff0000",
            "background-color": "#ff0000"
          }
        }
      }
    };
  },
  methods: {
    updateFormat(newFormat) {
      this.widgetData.format = { ...newFormat };
    },
    updateAnimation(newAnimation) {
      this.widgetData.animation = { ...newAnimation };
    },
    updateStyle(newStyle) {
      this.widgetData.style = { ...newStyle };
    },
    addExam() {
      this.widgetData.exams.push({
        title: "",
        dates: ""
      });
    },
    updateExam(index, newExam) {
      this.widgetData.exams[index] = [...newExam];
    },
    removeExam(index) {
      this.widgetData.exams.splice(index, 1);
    }
  },
  components: {
    UniqueId,
    SettingExam,
    SettingFormat,
    SettingStyle,
    SettingAnimation
  }
};
</script>

<style>
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>
