<template>
    <div id="setting-style">
        <div class="setting-toggle">
            <h2 @click="isOpen = !isOpen">Style</h2>
        </div>
        <div class="setting-list" v-show="isOpen">
            <SubSettingStyle name="Exam Title"
                             :userStyle="titleStyle"
                             @update="updateTitleStyle"></SubSettingStyle>
            <SubSettingStyle name="Exam D-day"
                             :userStyle="ddayStyle"
                             @update="updateDdayStyle"></SubSettingStyle>
            <SubSettingStyle name="Exam Date"
                             :userStyle="dateStyle"
                             @update="updateDateStyle"></SubSettingStyle>
        </div>
    </div>
</template>

<script>
import SubSettingStyle from './sub/SubSettingStyle.vue'

export default {
    name: 'SettingStyle',
    props: {
        userStyle: {
            title: {
                'font-family': String,
                'font-size': Number,
                'color': String,
                'background-color': String,
            },
            dday: {
                'font-family': String,
                'font-size': Number,
                'color': String,
                'background-color': String,
            },
            date: {
                'font-family': String,
                'font-size': Number,
                'color': String,
                'background-color': String,
            },
        }
    },
    data() {
        return {
            isOpen: true,
            titleStyle: this.userStyle.title,
            ddayStyle: this.userStyle.dday,
            dateStyle: this.userStyle.date,
        };
    },
    methods: {
        updateTitleStyle(newTitleStyle) {
            var newStyle = {
                ...this.style,
                title: {...newTitleStyle},
            };
            this.$emit('update', newStyle);
        },
        updateDdayStyle(newDdayStyle) {
            var newStyle = {
                ...this.style,
                dday: {...newDdayStyle},
            };
            this.$emit('update', newStyle);
        },
        updateDateStyle(newDateStyle) {
            var newStyle = {
                ...this.style,
                Date: {...newDateStyle},
            };
            this.$emit('update', newStyle);
        },
    },
    components: {
        SubSettingStyle,
    },
}
</script>