<template>
    <div id="setting-format">
        <div class="setting-toggle">
            <h2 @click="isOpen = !isOpen">Formatting</h2>
        </div>
        <div class="setting-list" v-show="isOpen">
            <div class="setting-row">
                <label>D-day Format</label>
                <select v-model="ddayFormat" @change="update">
                    <option v-for="(format, i) in ddayFormats" :value="format" :key="i">
                        {{ format }}
                    </option>
                </select>
            </div>
            <div class="setting-row">
                <label>Date Format</label>
                <select v-model="dateFormat" @change="update">
                    <option v-for="(format, i) in dateFormats" :value="format" :key="i">
                        {{ format }}
                    </option>
                </select>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: 'SettingFormat',
    props: {
        format: {
            dday: String,
            date: String,
        },
    },
    data() {
        return {
            isOpen: true,
            ddayFormats: [
                "D-28 (D-%d)",
                "D 28 (D %d)",
                "D-day 28 (D-day %d)",
                "D day 28 (D day %d)",
            ],
            dateFormats: [
                "2019.02.19 (YYYY.MM.DD)",
                "2019-02-19 (YYYY-MM-DD)",
                "19.02.19 (YY.MM.DD)",
                "19-02-19 (YY-MM-DD)",
                "190219 (YYMMDD)",
            ],
            ddayFormat: this.format.dday,
            dateFormat: this.format.date,
        };
    },
    methods: {
        update() {
            var newFormat = {
                dday: this.ddayFormat,
                date: this.dateFormat,
            };
            this.$emit('update', newFormat);
        },
    },
}
</script>